<template>
  <div>
    <b-button @click="insertDepartement" :disabled="isBusy">
      insert les departements
    </b-button>
    <b-table
      striped
      hover
      :items="rowsFile"
      :fields="headerFile"
      :per-page="perPage"
      :current-page="currentPage"
    ></b-table>
    <b-pagination
      v-model="currentPage"
      :total-rows="totalRows"
      :per-page="perPage"
      align="fill"
      size="sm"
      class="pt-5 pb-5 mt-5 mb-0"
      v-if="totalRows > 20"
    ></b-pagination>
  </div>
</template>
<script>
import { mapState } from "vuex";
import utilities from "drupal-vuejs/src/App/utilities";
utilities.TestDomain = "http://v2lesroisdelareno.kksa";
export default {
  name: "displayRowCsv",
  props: {
    items: {
      type: Array,
      required: true,
    },
    fields: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      currentPage: 1,
      perPage: 50,
      isBusy: false,
    };
  },
  computed: {
    ...mapState(["headerFile", "rowsFile"]),
    totalRows() {
      if (this.rowsFile.length) {
        return this.rowsFile.length;
      } else {
        return 0;
      }
    },
  },
  methods: {
    insertDepartement() {
      this.isBusy = true;
      const vid = "departement_de_france_2";
      const buildDeparts = () => {
        const departement = [];
        const preparDepart = [];

        this.rowsFile.forEach((row) => {
          if (row[12] && row[12] !== "" && !preparDepart.includes(row[12])) {
            preparDepart.push(row[12]);
            departement.push({
              vid: vid,
              name: row[12],
            });
          }
        });
        utilities
          .post("/import-entity-progress/createterms", {
            terms: departement,
          })
          .then(() => {
            this.isBusy = false;
          });
      };
      buildDeparts();
    },
  },
};
</script>
